
*{
  box-sizing: border-box;
}

body{
  margin: 0;
  background: #e9ebee;
  color: #1d2129;
}

.Navbar{
  color: #ffffff;
  padding: 10px;
  background: #4167b2;
}

.Input, .Post{
  border: 1px solid hsl(216, 8%, 88%);
  border-radius: 4px;
  width: 100%;
  margin: 20px auto;
  background-color: #ffffff;
}

.Input__header{
  background-color: #f5f6f7;
  border-bottom: 1px solid #dddfe2;
  border-radius: 2px 2px 0 0;
  margin: 0;
  padding: 15px;
}

.Input__field{
  width: 100%;
  height: 100%;
  padding: 10px;
  font-size: 20px;
  background-color: #ffffff;
  border: none;
}

.Input__field:focus{
  outline: none;
}

.Post{
  padding: 15px;
}

.Post__image{
  width: 100%;
  margin-top: 15px;
}

.Post__delete{
  border: none;
  background: none;

  padding: 0;
  float: right;

  cursor: pointer;
}